import React from 'react'
import { ThreeDots } from  'react-loader-spinner'
function Loader() {
  return (
    <div className='' id='loader' >
        <ThreeDots 
height="200" 
width="200" 
radius="29"
color="red" 
ariaLabel="three-dots-loading"
wrapperStyle={{}}
wrapperClassName=""
visible={true}
 />
    </div>
  )
}

export default Loader;