import React from 'react'
import "./Monvieinfo.css"
import Loader from './Loader'

import  { useState  } from "react";
import { useEffect } from 'react';
import {useParams} from "react-router-dom"
import { Link } from 'react-router-dom';
import "./App.css"
const Movieinfo = () => {


  const [loadings,setloading]=useState(true)


    const {id} =useParams()
    
    useEffect(()=>{
      credi()

        getData()
        similar()
        trailer()
 
        


      },[id]
    )

    
    
    const [ Cred,setcredits ] = useState([])
 
    const[currentmoviedetail,setMovie]=useState([]);
const [ popularMovies, setPopularMovies ] = useState([])
const [ trailers, settrailer ] = useState([])

    const getData=()=>{
        const url = `https://api.themoviedb.org/3/movie/${id}?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US`;

        fetch(url)
        .then(res => res.json())
        .then(data => setMovie(data))
        
    
}
const credi =()=>{
      
  fetch(`https://api.themoviedb.org/3/movie/${id}/credits?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=1`)
  
  .then(res => res.json())
  .then(data => setcredits(data.cast));
  
}
 

const similar=()=>{
    const options = {method: 'GET', headers: {accept: 'application/json'}};

fetch(`https://api.themoviedb.org/3/movie/${id}/recommendations?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=1`)
  
.then(res => res.json())
.then(data => setPopularMovies(data.results))
setloading(false)
  }

 
  
const trailer=()=>{
  const options = {method: 'GET', headers: {accept: 'application/json'}};

fetch(`https://api.themoviedb.org/3/movie/${id}/videos?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=1`)

.then(res => res.json())
.then(data =>
  
  {
// console.log(data.results.length);
    if (data.results.length > 0) {
      settrailer(data.results[0])

      } else {
        alert("Trailer not found. please search another movie")
      }

  }

  )



}

    return (

<>
{  
    loadings?<Loader/>:
    <>

<div id='main1' className="main pt-1">
<div id='infocontainer' class="container text-center  " >

<img id='background' className='w-100' src={`https://image.tmdb.org/t/p/original${currentmoviedetail && currentmoviedetail.backdrop_path}` }  class="card-img-top "  alt="... "/>


    <div class="movie  ">
        <div class="row gy-3  justify-content-evenly">
            <div class="col-lg-4 col-sm-5 text-center mx-4 my-4 img-fluid min-width-100 height-auto">
                <div class="card " style={{width: "18rem", padding: "auto", margin: "auto" }}>
                    <img src={`https://image.tmdb.org/t/p/original${currentmoviedetail && currentmoviedetail.poster_path}` }  class="card-img-top "  alt="... "/>
                    
                </div>
            </div>

            <div id='cardinformation' class="col-lg-6 col-sm-5 text-center px-3 pt-2 ">
                <h1 id='title1' className='' > <span>   {currentmoviedetail ? currentmoviedetail.original_title:""} </span></h1> 
                <h5 className="mt-2" > IMDB Ratings:</h5><span>{currentmoviedetail ? currentmoviedetail.vote_average:""}</span>

                <h5 className="mt-2" >Release date:</h5>  <span>{currentmoviedetail ? currentmoviedetail.release_date:""}</span>
                <h5 className="mt-2" >Language:</h5> English,Hindi
                <h5 className="mt-4" >Overview:</h5> <span>{currentmoviedetail ? currentmoviedetail.overview:""}</span>
              

                
          
    </div>
    
    </div>
    </div>



    
    
     <div id='creditbg' style={{width:'100%'}}  className="row  w-100 pt-1 pb-5 text-center justify-content-center ">
    
    
    {
    Cred.slice(0, 6).map(cr=>(

    <div id='creditbgsk'  className="col-6 pt-3 col-md-2  ">
    
    <div class="card "  id='castcard' style={{width: "11rem",height:""}}>
    <img id='castposter' className='card-img-top' height={'100%'} width={'100%'} src={`https://image.tmdb.org/t/p/original${cr && cr.profile_path}`} />
    
     <h5 id='castname' className=' pt-1 text-center' >
    {cr && cr.name} 
    </h5>
    
    </div>
    
    </div>
    ))}
    
    
    </div> 
        

    
<div className="video-responsive w-100  py-2 ">
    <iframe
      width="100%"
      height="550vh"
      src={`https://www.youtube.com/embed/${trailers.key}?version=3&vq=hd720`}
      frameBorder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowFullScreen
      title="Embedded youtube"
    />
  </div>

    </div>

    </div>

    <div className="container  ">
<h1 id='similar' >similar movies</h1>

{


popularMovies.map(movies=>(
  

  
  <div onClick={()=>{window.scrollTo(0, 0)}} class="react-card-flip  p-1 m-2 ">
    {
    loadings?<Loader/>:

<>
    <Link  style={{textDecoration:'none'}} to={`/movie/${movies&&movies.id}`}>

<div className="card-content">

  <img id='poster' height={'100%'} width={'100%'} src={`https://image.tmdb.org/t/p/original${movies && movies.poster_path}`} />

  <h4 id='title' className=' fs-5 px-1 py-2  ' >{movies&&movies.original_title}</h4>

  </div>
  </Link>
  </>
}
  </div>
  

))}  
  </div>
  </>
    }

</>
    
  )
}

export default Movieinfo
