import React from 'react'
import { useParams } from 'react-router-dom';
import "./App.css"
import "./index.css"
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Link } from "react-router-dom";
import Loader from './Loader';
import { useEffect } from 'react'
import { useState } from 'react'
const Getmovie = () => {




  const [loadings,setloading]=useState(true)
 
 const [movies,setmovie]= useState([''])
const [search, setsearch] = useState('')
const {type} = useParams()
const[page,setpage]=useState(1)


const changesearch=(event)=>{
setsearch(event.target.value);
}
 const getdata= async ()=>{
const res = await fetch(`https://api.themoviedb.org/3/movie/${type?type:"popular"}?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=${page}`)
const data = await res.json();
setmovie(data.results)
setloading(false)
}


const getsearch= async ()=>{
  const res = await fetch(`https://api.themoviedb.org/3/search/movie?query=${search}&api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=${page}`)
  const data = await res.json();
  setmovie(data.results)
  console.log(data.results);
setloading(false)

  }
  

useEffect(() => {
  
  


    }, [type,page])



    if (search==="") {
      getdata();
      
    }else{
        
      getsearch()
    }
    

  return (
    <>
    
<div className='container1' >
  
  <div class="container">
    
            <input value={search} placeholder="search movies" className="input" onChange={changesearch} type="" name="" />
<button id='rowbtn' >
<svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-search" viewBox="0 0 16 16">
  <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
</svg>

</button>


            <h1 id='heading' className=' text-uppercase d-block w-100' >{type?type:""}</h1>

            

            {
    loadings?<Loader/>:
<>
  {movies.map(movies=>(
  

  <div onClick={()=>{ window.scrollTo(0, 0)}} class="react-card-flip  p-1 m-2 ">
    <Link  style={{textDecoration:'none'}} to={`/movie/${movies&&movies.id}`}>

<div className="card-content">

  <img id='poster' height={'100%'} width={'100%'} src={`https://image.tmdb.org/t/p/original${movies && movies.poster_path}`} />

  <h4 id='title' className=' fs-5 px-1 py-2  ' >{movies&&movies.original_title}</h4>

  </div>
  </Link>

  </div>
  
))}

</>}


<div id='npbtn' className="text-center text-md-end row my-5 pb-2 container justify-content-center justify-content-md-end ">
  <button onClick={()=>{setpage(page-1);   window.scrollTo(0, 0)  }} className=" col-5 col-md-2 ">
  <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="currentColor" class="me-2 bi bi-skip-backward-fill" viewBox="0 0 16 16">
  <path d="M.5 3.5A.5.5 0 0 0 0 4v8a.5.5 0 0 0 1 0V8.753l6.267 3.636c.54.313 1.233-.066 1.233-.697v-2.94l6.267 3.636c.54.314 1.233-.065 1.233-.696V4.308c0-.63-.693-1.01-1.233-.696L8.5 7.248v-2.94c0-.63-.692-1.01-1.233-.696L1 7.248V4a.5.5 0 0 0-.5-.5"/>
</svg>
    prev</button>
  <button onClick={()=>{setpage(page+1); window.scrollTo(0, 0)}} className=" col-5 col-md-2">next
  <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="currentColor" class="bi bi-fast-forward-fill ms-2" viewBox="0 0 16 16">
  <path d="M7.596 7.304a.802.802 0 0 1 0 1.392l-6.363 3.692C.713 12.69 0 12.345 0 11.692V4.308c0-.653.713-.998 1.233-.696z"/>
  <path d="M15.596 7.304a.802.802 0 0 1 0 1.392l-6.363 3.692C8.713 12.69 8 12.345 8 11.692V4.308c0-.653.713-.998 1.233-.696z"/>
</svg>
  </button>

</div>

  </div>



  </div>
  
</>
    

  )
}


export default Getmovie