import React from 'react'
import "./App.css"
import { useState ,useEffect} from 'react';
import Getmovie from './getmovie';
import { Link } from "react-router-dom"
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
const Header = () => {
  const [theme, settheme] = useState("light-theme")
  const tog=document.getElementById('toggle');
  const toggle=()=>{
    if (theme==="dark-theme") {
      document.getElementById('toggle').style.color="white";
      document.getElementById('toggle').innerHTML="   <i class='bi bi-brightness-high-fill'></i>";
      settheme('light-theme')
      }else{
        document.getElementById('toggle').style.color="black";
        document.getElementById('toggle').innerHTML=" <i class='bi bi-moon-stars-fill'></i> ";
document.getElementById('navbar-tog').style.border="none";
        settheme("dark-theme")
      }
  }
  useEffect(()=>{
document.body.className=theme;
  },[theme])
  return (
    <>
<nav id='navbar1' class="navbar sticky-top navbar-expand-lg navbar-light  ">
  <div id='headercont' class="container-fluid text-black ">
    <a class="navbar-brand " id='logo' href="#">MOVIEZZZ
    
    <svg id='svg1' xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-camera-reels-fill pb-2" viewBox="0 0 16 16">
  <path d="M6 3a3 3 0 1 1-6 0 3 3 0 0 1 6 0z"/>
  <path d="M9 6a3 3 0 1 1 0-6 3 3 0 0 1 0 6z"/>
  <path d="M9 6h.5a2 2 0 0 1 1.983 1.738l3.11-1.382A1 1 0 0 1 16 7.269v7.462a1 1 0 0 1-1.406.913l-3.111-1.382A2 2 0 0 1 9.5 16H2a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h7z"/>
</svg>
    </a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span id='navbar-tog' class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav text-end gap-4  me-4 ms-auto mb-2 mb-lg-0">
        
      
        <li onClick={window.scrollTo(0, 0)} class="nav-item  ">
        <Link onClick={()=>{window.scrollTo(0, 0)}} to="/movies/now_playing" id='link' className='  ' style={{textDecoration: "none"}}><span>Now playing</span></Link>
        </li>
        <li onClick={()=>{window.scrollTo(0, 0)}} class="nav-item">
        <Link to="/movies/top_rated" id='link' style={{textDecoration: "none"}}><span>Top-rated</span></Link>
        </li>
        <li onClick={()=>{window.scrollTo(0, 0)}} class="nav-item">
        <Link to="/movies/upcoming" id='link' style={{textDecoration: "none"}}><span>Upcoming</span></Link>
        </li>
        <li class="nav-item">
<button id='toggle' className=''  onClick={()=>{toggle()}} ><i class=" text-light bi bi-brightness-high-fill"></i>  </button>
        </li>
        <li class="nav-item">
        </li>
        
      </ul>
    </div>
  </div>
</nav>
{/* 

    <Navbar expand="lg" className="bg-body-tertiary">
      <Container>
        <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
          <Link   style={{textDecoration: "none"}} to="/"><span id='logo' >MOVIEZZ</span></Link>
                <Link to="/movies/top_rated" style={{textDecoration: "none"}}><span>Top Rated</span></Link>
                <Link to="/movies/upcoming" style={{textDecoration: "none"}}><span>Upcoming</span></Link>
          
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
 */}

    </>
  )
}

export default Header;